@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#background {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: url(/static/media/starrynight.5d689918.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.App {
  display: grid;
}

.lucas-content {
  z-index: 1;
}

.lucas-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 0;
  padding-top: 3%;
}

.lucas-page .MuiCardContent-root:last-child {
  padding-bottom: 16px;
}

.center-column {
  width: 95%;
}

.lucas-section-title {
  font-family: 'Raleway', sans-serif;
  font-weight: 200;
  font-size: 12vw;
  text-align: center;
  color: white;
}

.lucas-subtitle {
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  font-size: xx-large;
  text-align: center;
  color: white;
}

.lucas-divider {
  color: white;
  width: 95%;
  margin-top: 32px;
  margin-bottom: 32px;
}

.lucas-content {
  margin: 3%;
  margin-top: 5%;
  margin-bottom: 5%;
}

@media only screen and (min-width: 1240px) {

  .lucas-page {
    padding-top: 1%;
  }
  
  .center-column {
    width: 1170px;
  }

  .column-half {
    width: 50%;
  }
  
  .column-golden-larger {
    width: 62%;
  }
  
  .column-golden-smaller {
    width: 38%
  }

  .lucas-divider {
    width: 1170px
  }

  .lucas-section-title {
    font-size: 10vh;
  }

  .lucas-section {
    width: 1170px;
    -webkit-animation: cd-bounce-2 .6s;
            animation: cd-bounce-2 .6s;

  }

  .lucas-content {
    margin: 2%;
  }
}
#introductions {
    flex-direction: column;
}

.homepage-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.homepage-nav .MuiFab-root {
    font-size: 1.5rem;
    width: 3rem;
    height: 3rem;
    min-width: 3rem;
    min-height: 3rem;
    margin-left: 16px;
    box-shadow: none;
    
}

.homepage-nav .MuiCardContent-root {
    margin-left: 2%;
}

@media only screen and (min-width: 1240px) {
    .homepage-nav-group {
        display: flex;
        flex-direction: row;
        width: 100%;
    }
}
#portrait {
    border-radius: 0.25rem;
}

.lucas-content img {
    width: 100%;
    object-fit: cover;
}

@media only screen and (min-width: 1240px) {
    #about .homepage-section-body {
        display: flex;
    }
    
    #about .homepage-section-body .lucas-content {
        margin-bottom: 0;
    }
    
    .homepage-about {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }
}
#contact {
    margin-bottom: 10vh;
}

.homepage-contact-fabs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.homepage-contact-fabs .MuiFab-root {
    font-size: 2.5rem;
    width: 5rem;
    height: 5rem;
    min-width: 5rem;
    min-height: 5rem;
    margin-left: 5vw;
    margin-right: 5vw;
    box-shadow: none;
}

@media only screen and (min-width: 1240px) {
    .homepage-contact-fabs .MuiFab-root {
        font-size: 3rem;
        width: 6rem;
        height: 6rem;
        min-width: 6rem;
        min-height: 6rem;
    }
}
.homepage-home {
    height: 88vh;
    display: flex;
    justify-content: center;
}

.homepage-big-text {
    position: relative;
    top: 12vh;
    font-size: 14vw;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    text-align: center;
    color: white;
}

#homepage-logo-horz {
    display: none;
}

#homepage-logo-vert {
    object-fit: contain;
    position: relative;
    width: 60%;
    height: auto;
}

