@media only screen and (min-width: 1240px) {

    #homepage-logo-horz {
        display: block;
        object-fit: contain;
        position: relative;
        bottom: 10vh;
    }

    #homepage-logo-vert {
        display: none;
    }

    .homepage-half {
        width: 50%;
        position: relative;
    }

    .homepage-divider {
        width: 1170px
    }

    .homepage-section-title {
        font-size: 10vh;
    }

    .homepage-section {
        width: 1170px;
    }

    .homepage-section-body {
        display: flex;
        flex-direction: row;
    }
}
.timeline-visual {
    object-fit: contain;
    width: 100%;
    height: auto;
    max-height: 320px;
    border-radius: 0.25rem;
    background-color: darkgray;
}
.timeline-button-container {
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
}

.timeline-button-container .MuiFab-root {
    font-size: 1.5rem;
    width: 3rem;
    height: 3rem;
    box-shadow: none;
    text-align: center;
}

@media only screen and (min-width: 1240px) {
    .timeline-button-container {
        flex-direction: row;
        width: 45%;
        position: absolute;
        bottom: 0px;
    }
}
.timeline-date {
    color: gray;
}

.timeline-text {
    color: gray;
    padding-left: 3%;
}

.timeline-text h3 {
    color: rgb(69, 69, 69);
}

.SnapImg {
    object-fit: contain;
    width: 100%;
}

@media only screen and (min-width: 1240px) {
    .timeline-body {
        display: flex;
        flex-direction: row;
    }

    .timeline-half {
        width: 50%;
    }
}

.MuiFormControlLabel-root{
    padding-top: 12px;
    width: 60px;
}

.MuiFormGroup-row {
    justify-content: space-evenly;
}
