    }

    #funpage-marching-band {
        flex-direction: row;
    }
    
    #funpage-indoor-percussion {
        display: flex;
        flex-direction: row;
    }
}
.funpage-design-content {
    max-height: 900px;
    width: 100%;
    object-fit: contain;
}

